
.shopping_cart_page{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.shopping_card_page_content1{
    width: 90%;
    height: 100%;
    padding-top: 7rem;
    padding-bottom: 5rem;
}

.shopping_card_page_content1_title{
    font-size: 1.8rem;
    color: var(--black-color);
}

.shopping_card_page_content1_flex{
    display: flex;
    flex-direction: row;
    /* position: relative; */
}

.shopping_card_page_content1_flex_fix{
    display: flex;
    flex-direction: row;
    height: 70vh;
    overflow: hidden;
}

.shopping_card_page_content1_items_div{
    width: 70%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2.5rem;
    margin-top: 2rem;
    position: relative;
}


.shopping_card_page_content1_sidebar{
    position: sticky;
    background-color: #F9F9F9;
    width: 30%;
    height: 100%;
    left: 100%;
    top: 0%;
    margin: 2rem 0 2rem 2rem;
    border-radius: 16px;
    box-shadow: 0px 4px 8px var(--shopping-cart-dark-gray-color);
}

.shopping_card_page_content1_sidebar_box{
    margin: 1rem;
    display: flex;
    flex-direction: column;
}

.shopping_card_page_content1_sidebar_title{
    font-size: 1.5rem;
    font-weight: 500;
    color: var(--black-color);
}

.shopping_card_page_content1_sidebar_box_total_amount{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0.5rem 0;
}

.shopping_card_page_content1_sidebar_box_total{
    font-size: 0.9rem;
    color: var(--shopping-cart-dark-gray-color);
}

.shopping_card_page_content1_sidebar_box_amount{
    color: var(--shopping-cart-yellow-color);
}

.shopping_card_page_content1_sidebar_hr{
    height: 0.5px;
    width: 100%;
    border: none;
    background-color: #999999;
}

.shopping_card_page_content1_sidebar_box_coupan_code_box{
    width: 100%;
    height: 40px;
    display: flex;
    flex-direction: row;
    border-radius: 3px;
    overflow: hidden;
    margin: 0.5rem 0;
}

.shopping_card_page_content1_sidebar_box_coupan_code{
    width: 70%;
    border: none;
    outline: none;
    background-color: #F3F3F3;
    color: var(--black-color);
    padding: 0.5rem;
}

.shopping_card_page_content1_sidebar_box_coupan_code_button{
    width: 30%;
    border-radius: 3px;
    overflow: hidden;
    border: none;
    background-color: var(--shopping-cart-yellow-color);
    font-size: 1.1rem;
    color: var(--white-color);
}

.shopping_card_page_content1_sidebar_box_order_now_button{
    width: 100%;
    height: 40px;
    margin: 0.5rem 0;
    border-radius: 3px;
    overflow: hidden;
    border: none;
    background-color: var(--shopping-cart-yellow-color);
    font-size: 1.1rem;
    color: var(--white-color);
}

@media (min-width: 424px) and (max-width: 768px) {
    .shopping_card_page_content1{
        padding-top: 6rem;
        padding-bottom: 0rem;
    }
    .shopping_card_page_content1_title{
        font-size: 1.4rem;
    }
    .shopping_card_page_content1_flex{
        flex-direction: column;
    }
    .shopping_card_page_content1_items_div{
        width: 100%;
        margin-bottom: 1.5rem;
    }
    .shopping_card_page_content1_sidebar{
        /* display: none; */
        width: 100%;
        margin: 0;
        position: relative;
        bottom: 0%;
        left: 0;
    }
}

@media (max-width: 426px) {
    .shopping_card_page_content1{
        padding-top: 6rem;
        padding-bottom: 0rem;
    }
    .shopping_card_page_content1_title{
        font-size: 1.2rem;
    }
    .shopping_card_page_content1_flex{
        flex-direction: column;
    }
    .shopping_card_page_content1_items_div{
        width: 100%;
        margin-bottom: 1.5rem;
        grid-template-columns: repeat(1, 1fr);
        gap: 1.5rem;
        margin-top: 1rem;
    }
    .shopping_card_page_content1_sidebar{
        /* display: none; */
        width: 100%;
        margin: 0;
        position: sticky;
        bottom: 0%;
    }

    .shopping_card_page_content1_sidebar_title{
        font-size: 1.1rem;
    }
    .shopping_card_page_content1_sidebar_box_total{
        font-size: 0.8rem;
    }
    .shopping_card_page_content1_sidebar_box_total{
        font-size: 0.8rem;
    }
    .shopping_card_page_content1_sidebar_box_total_amount,
    .shopping_card_page_content1_sidebar_box_total_amount,
    .shopping_card_page_content1_sidebar_box_total_amount{
        margin: 0.35rem 0;
    }
    .shopping_card_page_content1_sidebar_box_coupan_code_button,
    .shopping_card_page_content1_sidebar_box_order_now_button{
        font-size: 0.9rem;
    }
}

/* -------------------- Receipe box ------------------------*/

.receipt_main{
    height: 100vh;
    width: 100vw;
    background-color: #99999999;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
}

.receipt_main_none{
    display: none;
}

.receipt{
    z-index: 10;
    width: 33%;
    background-color: white;
    border-radius: 1rem;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border: 1px solid gray;
    padding-bottom: 2rem;
    position: relative;
}

.receipt_none{
    display: none;
}

.receipt_box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.orderdone{
    font-size: 2.2rem;
    font-weight: 600;
}

.download{
    width: 250px;
    height: 40px;
    margin: 0.5rem 0;
    border-radius: 3px;
    overflow: hidden;
    border: none;
    background-color: var(--shopping-cart-yellow-color);
    font-size: 1.1rem;
    color: var(--white-color);
}


@media (min-width: 768px) and (max-width: 1024px){
    .receipt{
        width: 40%;
    }
    .orderdone{
        font-size: 2rem;
    }
}

@media (min-width: 425px) and (max-width: 768px){
    .receipt{
        width: 80%;
    }
    .orderdone{
        font-size: 2rem;
    }
}

@media (max-width: 425px) {
    .receipt{
        width: 90%;
    }
    .orderdone{
        font-size: 1.4rem;
    }
    .download{
        width: 180px;
        font-size: 1rem;
    }
}

