.navbar_container{
    width: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar_s{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    box-shadow: 0px 4px 8px rgba(17, 17, 17, 0.59);
    position: fixed;
    top: 0;
    z-index: 1000;
}

.navbar_div{
    height: 5rem;
    width: 85%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.header_left{
    color: var(--shopping-cart-yellow-color);
    font-size: 1.1rem;
    font-weight: 500;
}

.header_right{
    display: flex;
    align-items: center;
    gap: 4rem;
}

#header_menu{
    color: var(--white-color);
    font-size: 1.1rem;
}

#header_shopping{
    position: relative;
}

#header_shopping_icon{
    height: 2rem;
}

