.header_right a div p{
    font-size: 11px;
    color: var(--white-color);
}

.usericon{
    height: 2rem;
    width: 2rem;
    background: var(--shopping-cart-yellow-color);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}

.username{
    color: black;
    text-transform: uppercase;
    font-weight: 500;
}

@media (min-width: 768px) and (max-width: 1024px) {
    .navbar_container{
        width: 100vw;
    }
    .navbar_div{
        width: 90%;
    }
}

@media (min-width: 425px) and (max-width: 767px) {
    .navbar_container{
        width: 100vw;
    }
    .navbar_div{
        width: 90%;
    }
}

@media (min-width: 320px) and (max-width: 424px) {
    .navbar_container{
        width: 100vw;
    }
    .navbar_div{
        width: 90%;
    }
    .header_left{
        font-size: 0.9rem;
    }
    .header_right{
        gap: 1.5rem;
    }
    #header_menu{
        font-size: 0.9rem;
    }
    #header_shopping_icon{
        height: 1.4rem;
    }
    .header_right a div p{
        font-size: 8px;
    }
    .usericon{
        width: 1.8rem;
        height: 1.8rem;
    }
}

@media (max-width: 320px){
    .navbar_container{
        width: 100vw;
    }
    .navbar_div{
        width: 90%;
    }
    .header_left{
        font-size: 0.9rem;
    }
    .header_right{
        gap: 0.8rem;
    }
    #header_menu{
        font-size: 0.9rem;
    }
    #header_shopping_icon{
        height: 1.4rem;
    }
    .header_right a div p{
        font-size: 8px;
    }
    .usericon{
        width: 1.5rem;
        height: 1.5rem;
    }
}