.scroll_btn{
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    border: 1px solid rgba(128, 128, 128, 0.454);
    transition: all .3s ease-in;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--shopping-cart-yellow-color);
    /* fixed bottom-4 right-4 h-12 w-12 rounded-full border-2 hover:border-yellow-700 */
}

.scroll_btn:hover{
    background-color: var(--yellow-color);
    border: 2px solid rgba(221, 241, 4, 0.41);
}

.arrow{
    height: 1.5rem;
    width: 1.5rem;
    /* h-6 w-6 */
}

@media (min-width: 768px) and (max-width: 1024px) {
    .scroll_btn{
        height: 2.5rem;
        width: 2.5rem;
    }
    .arrow{
        height: 1.2rem;
        width: 1.2rem;
    }
}

@media (min-width: 425px) and (max-width: 768px) {
    .scroll_btn{
        height: 2rem;
        width: 2rem;
    }
    .arrow{
        height: 1rem;
        width: 1rem;
    }
}

@media (max-width: 425px) {
    .scroll_btn{
        height: 1.6rem;
        width: 1.6rem;
    }
    .arrow{
        height: 0.8rem;
        width: 0.8rem;
    }
}