@media (min-width: 768px) and (max-width: 1024px) {
    .popular_recipes_lists{
        margin-top: 1rem;
    }
    .popular_recipes_button{
        height: 50px;
        width: 120px;
    }
}

@media (min-width: 425px) and (max-width: 767px) {
    #popular_recipes_header_name{
        font-size: 1.8rem;
    }
    #popular_recipes_header_img {
        height: 6rem;
    }
    .popular_recipes_lists{
        gap: 0.5rem;
        margin-top: 1rem;
    }
    .popular_recipes_button {
        height: 40px;
        width: 70px;
        font-size: 0.7rem;
    }
}

@media (max-width: 426px) {
    .popular_recipes_box_image{
        margin-top: 15px;
    }
    #popular_recipes_box_img{
        height: 190px;
    }
    .popular_recipes_box{
        height: 400px;
        min-width: 250px;
        margin-left: 1rem;
        margin-right: 1rem;
    }
    #popular_recipes_box_name{
        font-size: 0.9rem;
    }
    #popular_recipes_box_time{
        font-size: 0.6rem;
    }
    #popular_recipes_box_address{
        font-size: 0.6rem;
    }
    #popular_recipes_box_price{
        font-size: 1.25rem;
    }
}

@media (min-width: 375px) and (max-width: 424px) {
    .popular_recipes{
        margin-top: 60px;
    }
    #popular_recipes_header_name{
        font-size: 1.5rem;
    }
    #popular_recipes_header_img {
        height: 4rem;
    }
    .popular_recipes_lists{
        gap: 0.4rem;
        margin-top: 1rem;
    }
    .popular_recipes_button {
        height: 30px;
        width: 60px;
        font-size: 0.6rem;
    }
}

@media (max-width: 374px) {
    .popular_recipes{
        margin-top: 50px;
    }
    #popular_recipes_header_name{
        font-size: 1.2rem;
    }
    #popular_recipes_header_img {
        height: 3rem;
    }
    .popular_recipes_lists{
        gap: 0.4rem;
        margin-top: 1rem;
    }
    .popular_recipes_button {
        height: 25px;
        width: 50px;
        font-size: 0.5rem;
    }
}